.button {
  font-family: 'Open Sans';
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  border: 2px solid #0A2640;
  border-radius: 24px;
  color: #0A2640;
  padding: 8px 40px;
  background-color: transparent;
  cursor: pointer; }
  .button:hover {
    content: "";
    transition: color ease 0.35s, transform ease 0.35s;
    transform: scale(1.1);
    color: #65E4A3; }
  .button.button {
    margin-right: 20px; }
  .button.button:last-child {
    margin-right: 0px; }
  .button.lg {
    padding: 16px 56px;
    font-size: 20px;
    line-height: 28px;
    border-radius: 56px; }
    @media (max-width: 480px) {
