        padding: 16px 38px;
        line-height: 16px;
        font-size: 16px;
        margin-bottom: 100px; } }
    .button.lg.button:last-child {
      margin-right: 0; }
    .button.lg.dark {
      background-color: #0A2640;
      color: #ffffff; }
    .button.lg.dark:hover {
      color: #65E4A3; }

